<template>
  <div>
    <div class="slideshow">
      <slide image="/static/images/slider/image-girl.png"></slide>
      <slide image="/static/images/slider/image-girl2.png"></slide>
      <div class="button-wrap button__arrow-right slider-button slider-button__left">
        <a @click="navigateToIdar" class="button__full button-border__regular button__slider "><span>Interior Design & architecture</span></a>
      </div>
      <div class="button-wrap button__arrow-right slider-button slider-button__right">
        <a @click="navigateToFashion" class="button__full button__full_inverse button-border__regular button__slider "><span>Fashion</span></a>
      </div>
    </div>
  </div>
</template>

<script>
  import anime from 'animejs'
  window.anime = anime;
  import './../../assets/libs/js/mirrorFx.js'
  import './../../assets/libs/js/slideshow.js'
  import smoke from '../../assets/libs/js/smoke.js'
  import slide from './gallery__slide.vue'
  export default {
    data(){
      return {
        slider: {}
      }
    },
    methods: {
      navigateToIdar(){
        this.$store.state.transitionName = 'slide-right';
        this.$router.push('/' + this.$route.params.lang + '/idar');
      },
      navigateToFashion(){
        this.$store.state.transitionName = 'slide-left';
        this.$router.push('/' + this.$route.params.lang + '/fashion');
      },
      nextSlide(){
        if (this.slider.itemsTotal > this.slider.current + 1)
          this.slider._navigate(this.slider.current + 1)
        else if (this.slider.itemsTotal = this.slider.current + 1)
          this.slider._navigate(0)
      }
    },
    components: {
      slide
    },
    mounted(){
      this.slider = new Slideshow(document.querySelector('.slideshow'));
      document.querySelector('.slideshow').appendChild( window.smoke );
      setInterval(this.nextSlide, 7000);
    }
  }
</script>

<style lang="sass" src="./../../assets/style/home-gallery.sass"></style>
<style lang="sass" src="./../../assets/style/styleguide.sass"></style>
<style src="./../../assets/libs/css/normalize.css"></style>
<style src="./../../assets/libs/css/demo.css"></style>
<style src="./../../assets/libs/css/mirror.css"></style>


