<script>
  export default {
    mounted() {
      let preloaderInit = function() {
        var preloaderBlock = document.getElementById("section-preloader");
        preloaderBlock.style.opacity = 1;
        var interPreloader = setInterval(function () {
          preloaderBlock.style.opacity = preloaderBlock.style.opacity - 0.05;
          if (preloaderBlock.style.opacity <= 0.05) {
            clearInterval(interPreloader);
            preloaderBlock.style.display = "none";
          }
        }, 16);
        this.$store.state.preloaderDone = true;
      };
      setTimeout(preloaderInit.bind(this), 3500);
    }
  }
</script>

<style lang="sass" src="./../../assets/style/preloader.sass"></style>


