<template>
  <div>
    <app-info first="image">
      <h4 slot="text" class="title">{{contact_info[$route.params.lang]['title']}}</h4>
      <div slot="text" class="description contacts" v-html="contact_info[$route.params.lang]['description']"></div>
      <a class="button__full button-border__regular" slot="text"
         @click="goToContact">{{contact_info[$route.params.lang]['button']}}</a>
      <div slot="image">
        <app-block-image
          class="bg-image image"
          :bg_image="contact_info[$route.params.lang]['image']"
        ></app-block-image>
      </div>
    </app-info>
  </div>
</template>

<script>
  import info from './info_divided.vue';
  import block_image from '../global/image_block.vue'

  export default {
    data(){
      return {
        contact_info: {
          ru: {
            image: '/static/img/Home_contact.png',
            title: '',
            description: '<p>+38 (044) 232 4242    +38 (067) 226 3377</p><p>.,   36/7</p><p>f-style@ukr.net</p>',
            button: ''
          },
          en: {
            title: 'Contacts',
            description: '<p>+38 (044) 232 4242    +38 (067) 226 3377</p><p>Shelkovichna str. Kyiv 36/7</p><p>f-style@ukr.net</p>',
            button: 'Details'
          },
        }
      }
    },
    mounted(){
      $('.match-height').matchHeight({byRow: true});
    },
    components: {
      appInfo: info,
      appBlockImage: block_image
    },
    methods: {
      goToContact(){
        this.$router.push('/' + this.$route.params.lang + '/contact');
      },
    }
  }
</script>

<style lang="sass" src="./../../assets/style/contact.sass"></style>


