<template>
  <div class="slide">
    <div class="mirror" data-visible-area="0.85">
      <div class="mirror__side mirror__side--one">
        <img class="mirror__img" :src="image" :alt="alt" />
      </div>
      <div class="mirror__side mirror__side--two">
        <img class="mirror__img" :src="image" :alt="alt" />
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      image: {
        type: String,
        required: true
      },
      alt: {
        type: String,
        default: 'Farnosova'
      }
    }
  }
</script>



