import Vue from 'vue'
import App from './App'
import store from './store'
import router from './router'
import resource from 'vue-resource'
import jquery from 'jquery'
import matchHeight from 'jquery-match-height'
// import onepage_scroll from './assets/libs/js/jquery.onepage-scroll.min'

Vue.use(resource);

window.$ = jquery;
window.$.matchHeight = matchHeight;
// window.$.onepage_scroll = onepage_scroll;

Vue.config.productionTip = false;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
});

//scrolltop after opening new page
router.afterEach(function() {
  window.scrollTo(0, 0);
  window.scrollIsDone = true;
  store.state.currentPath = router.history.current.fullPath;
  store.state.lang = router.history.current.fullPath.substr(1,2);
  store.state.nav.visible = false;
});


