              idar: ' ',
              fashion: '',
              services: '',
              farnosova: ' ',
              contact: ''
            },
            social: {
              instagram: {
                text: 'IN',
                url: 'https://www.instagram.com/nataly_farnosova_design_studio/'
              },
              pinterest: {
                text: 'P',
                url: 'https://www.pinterest.com/pin/567664728008063851/'
              },
              facebook: {
                text: 'F',
                url: 'https://www.facebook.com/pages/fSTYLE/107532029277973'
              },
              youtube: {
                text: 'YOU',
                url: 'https://www.youtube.com/user/TVFStyle'
              },
            }
          },
          en: {
            pages: {
              idar: 'ID&A',
              fashion: 'Fashion',
              services: 'Services',
              farnosova: 'Farnosova',
              contact: 'Contact'
            },
            social: {
              instagram: {
                text: 'IN',
                url: 'https://www.instagram.com/nataly_farnosova_design_studio/'
              },
              pinterest: {
                text: 'P',
                url: 'https://www.pinterest.com/pin/567664728008063851/'
              },
              facebook: {
                text: 'F',
                url: 'https://www.facebook.com/pages/fSTYLE/107532029277973'
              },
              youtube: {
                text: 'YOU',
                url: 'https://www.youtube.com/user/TVFStyle'
              },
            }
          }
        }
      }
    },
    filters: {
      alternateLang(value){
        if (value == 'ru') return 'en';
        else if (value == 'en') return 'ru';
      }
    },
    methods: {
      changeLang(){
        if (this.$route.params.lang == 'ru') {
          this.$router.push('/en' + this.$route.path.substring(3));
        }
        else if (this.$route.params.lang == 'en') {
          this.$router.push('/ru' + this.$route.path.substring(3));
        }
      }
    }
  }
</script>

