<template key="home">
  <div class="page page_home page_no-pagination" >
    <app-gallery>
    </app-gallery>
    <app-info :class="$store.state.currentPageSlide == 2?'current':''" first="text">
      <h4 slot="text" class="title">{{page_home[$route.params.lang]['info_1']['title']}}</h4>
      <p slot="text" class="description">{{page_home[$route.params.lang]['info_1']['description']}}</p>
      <a class="button__full button-border__regular" slot="text"
         @click="navigateToFarnosova">{{page_home[$route.params.lang]['info_1']['button']}}</a>
      <div slot="image">
        <farnosova></farnosova>
      </div>
    </app-info>
    <app-info :class="$store.state.currentPageSlide == 3?'current':''" first="image">
      <h4 slot="text" class="title">{{page_home[$route.params.lang]['info_2']['title']}}</h4>
      <a slot="text" class="button_arrow button_arrow__big white" @click="navigateToLastest">
        <svg version="1.1" class="svg-line" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
           viewBox="0 0 150 5" style="enable-background:new 0 0 151 7;" xml:space="preserve">
           <polygon class="svg_path-line" points="151,3.5 147,0 147,3 0,3 0,4 147,4 147,7  "/>
        </svg>
      </a>
      <div slot="image">
        <lastestProjectsImage></lastestProjectsImage>
      </div>
    </app-info>
    <app-info first="text">
      <h4 slot="text" class="title">{{page_home[$route.params.lang]['info_3']['title']}}</h4>
      <p slot="text" class="description">{{page_home[$route.params.lang]['info_3']['description']}}</p>
      <a slot="text" class=" button_arrow button_arrow__big white" @click="navigateToMedia">
        <svg version="1.1" class="svg-line" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            viewBox="0 0 150 5" style="enable-background:new 0 0 151 7;" xml:space="preserve">
            <polygon class="svg_path-line" points="151,3.5 147,0 147,3 0,3 0,4 147,4 147,7  "/>
        </svg>
      </a>
      <div slot="image">
        <!--<img src="/static/images/home__info-block_3.jpg" alt="">-->
        <app-block-image
          class="bg-image image"
          :bg_image="page_home[$route.params.lang]['info_3']['image']"
        ></app-block-image>
      </div>
    </app-info>
    <app-contact></app-contact>
  </div>
</template>

<script>
  import gallery from './home/gallery.vue';
  import banner from './idar/banner.vue';
  import info from './global/info_divided.vue';
  import farnosova from './home/farnosova.vue';
  import lastestProjectsImage from './home/lastestProjectsImage.vue';
  import contact from './global/contact.vue';
  import block_image from './global/image_block.vue';
  import functions from './../functions'
  export default {
    data(){
      return {
        one_page_scroll: {
          enable: true,
          navigation: true
        },
        page_home: {
          ru: {
            gallery: {
              button_idar: ' ',
              button_fashion: ''
            },
            info_1: {
              title: ' ',
              description: '    .           - .',
              button: '',
              image: '/static/img/Home_about.png'
            },
            info_2: {
              title: '  ',
              image: '/static/img/Home_lastwork.png'
            },
            info_3: {
              title: '  ',
              description: '     ',
              image: '/static/img/Home_news.png'
            }
          },
          en: {
            gallery: {
              button_idar: 'Interiour design',
              button_fashion: 'Fashion'
            },
            info_1: {
              title: 'Natalya Farnosova',
              description: 'lorem ipsum',
              button: 'more info'
            },
            info_2: {
              title: 'Lastest projects',
            },
            info_3: {
              title: 'Media',
              description: 'lorem'
            }
          }
        }
      }
    },
    components: {
      appBlockImage: block_image,
      appGallery: gallery,
      appInfo: info,
      farnosova,
      lastestProjectsImage,
      appIdarGallery: contact,
      appContact: contact
    },
    methods: {
      navigateToFarnosova(){
        this.$router.push('/' + this.$route.params.lang + '/farnosova');
      },
      navigateToLastest(){
        this.$router.push('/' + this.$route.params.lang + '/gallery/lastest/idar');
      },
      navigateToMedia(){
        this.$router.push('/' + this.$route.params.lang + '/media');
      }
    },
    created(){
      //functions.getPageInfo();
    },
  }
</script>

<style lang="sass" src="./../assets/style/styleguide.sass"></style>
<style lang="sass" src="./../assets/style/global.sass"></style>
<style lang="sass" src="./../assets/style/page_home.sass"></style>


