import store from './store'
export default {
  getPageInfo(){
    fetch('http://farnosovadmin2.krokus.info/page/home')
      .then((response) => {console.log(response)});
  },
  onePageScroll(direction){
    let currentPos,
        windowHeight,
        stepSize,
        step,
        finalPos,
        i,
        scrollIime,
        stepsNumber,
        stepsInterval;
    scrollIime = 500; //set scroll time to 1 second
    windowHeight = window.innerHeight;
    stepSize = 10; //set step size to 5 px
    store.state.scrollIsDone = false;
    currentPos = window.scrollY;
    stepsNumber = Math.floor(windowHeight / stepSize);
    stepsInterval = Math.floor(scrollIime / stepsNumber);
    if (direction =='up') {
      finalPos = currentPos - windowHeight;
      step = (-1)*stepSize;
    }
    else if (direction == 'down') {
      finalPos = currentPos + windowHeight;
      step = stepSize;
    }
    if ((finalPos + windowHeight) < this.documentHeight()) {
      i = setInterval(function () {
        window.scroll(0, currentPos + step);
        currentPos = window.scrollY;
        if ((currentPos == finalPos) || (i > stepsNumber)) {
          window.clearInterval(i);
          store.state.scrollIsDone = true;
        }
      }, stepsInterval);
    }
    else store.state.scrollIsDone = true;
  },
  documentHeight(){
    var body = document.body,
        html = document.documentElement;
    return Math.max( body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight );
  }
}


