<template>
  <div
    key="initial"
    :title="alt"
    class="block_image"
    :style="staticStyle">
    <slot></slot>
  </div>
</template>

<script>
export default {
  data(){
    return {
      dynamicStyle: {},
    }
  },
  props: {
    bg_image: {
      type: String,
      default: "/static/images/image_none.png"
    },
    alt: {
      type: String,
      default: "FARNOSOVA"
    },
  },
  computed: {
    staticStyle(){
      let v = this;
      let s = {
        backgroundImage: 'url(' + v.bg_image + ')',
      };
      return s;
    }
  },
}
</script>

<style lang="sass" src="./../../assets/style/image_block.sass"></style>



