<template>
  <div class="col-xs-12">
    <div class="row h100">
      <div class="banner" :style="{ backgroundImage: 'url(' + image + ')' }">
        <h1 class="banner__title" v-if="text">{{text}}</h1>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
	export default {
		props: {
			image: {
				type: String,
			},
			text: {
				type: String,
			}
		}
	}
</script>

<style src="./../../assets/style/banner.sass" lang="sass"></style>


