<template>
  <div>
    <app-preloader v-if="!this.$store.state.preloaderDone"></app-preloader>
    <app-nav-global v-if="this.$store.state.preloaderDone"></app-nav-global>
    <!--<h1 class="white">{{this.$store.state.scrollIsDone}}</h1>-->
    <transition
      :name="$store.state.transitionName"
      @after-enter="$store.state.transitionName = 'fade'">
      <router-view v-if="this.$store.state.preloaderDone"></router-view>
    </transition>
  </div>
</template>

<script>
  import preloader from './components/global/preloader.vue';
  import nav_global from './components/global/nav_global.vue';
  import f from './functions'
  export default {
    data(){
      return {
        transitionName: ''
      }
    },
    name: 'app',
    components: {
      appNavGlobal: nav_global,
      appPreloader: preloader
    },
    methods: {
      handleScroll(e){
        let scrollDirection;
        e.wheelDelta < 0 ? scrollDirection = 'down' : scrollDirection = 'up';
        if (typeof (this.$store.state.scrollIsDone) == 'undefined') this.$store.state.scrollIsDone = true;
        if(this.$store.state.scrollIsDone) f.onePageScroll(scrollDirection);
      }
    },
    mounted(){
      //don't need to remove these event listeners
//      window.addEventListener('mousewheel', function(e){
//        e.preventDefault();
//        this.handleScroll(e);
//      }.bind(this));
    },
    watch:{
      '$store.state.scrollIsDone'(isDone){
        if( isDone ) { //enter animations
          this.$store.state.currentPageSlide = (window.scrollY / window.innerHeight) + 1;
        }
      }
    }
  }
</script>

<style src="./assets/style/transitions.sass" lang="sass"></style>
<style src="./assets/libs/css/bootstrap.css"></style>


