import Vue from 'vue'
import Router from 'vue-router'
import store from './../store'
import Home from '@/components/Home.vue';

//lazy load
const Idar = resolve => {
	require.ensure(['@/components/Idar.vue'], () => {
		resolve(require('@/components/Idar.vue'));
	});
};
const Fashion = resolve => {
	require.ensure(['@/components/Fashion.vue'], () => {
		resolve(require('@/components/Fashion.vue'));
	});
};
const Gallery_fashion = resolve => {
	require.ensure(['@/components/gallery/Gallery_fashion.vue'], () => {
		resolve(require('@/components/gallery/Gallery_fashion.vue'));
	});
};
const Services = resolve => {
	require.ensure(['@/components/Services.vue'], () => {
		resolve(require('@/components/Services.vue'));
	});
};
const Farnosova = resolve => {
	require.ensure(['@/components/Farnosova.vue'], () => {
		resolve(require('@/components/Farnosova.vue'));
	});
};
const Contact = resolve => {
	require.ensure(['@/components/Contact.vue'], () => {
		resolve(require('@/components/Contact.vue'));
	});
};
const Page_404 = resolve => {
	require.ensure(['@/components/Page_404.vue'], () => {
		resolve(require('@/components/Page_404.vue'));
	});
};
const Gallery_lastest = resolve => {
	require.ensure(['@/components/gallery/Gallery_lastest.vue'], () => {
		resolve(require('@/components/gallery/Gallery_lastest.vue'));
	});
};
const Gallery_idar_project = resolve => {
	require.ensure(['@/components/gallery/Gallery_idar-project.vue'], () => {
		resolve(require('@/components/gallery/Gallery_idar-project.vue'));
	});
};

const Idar_items = resolve => {
	require.ensure(['@/components/Idar_items.vue'], () => {
		resolve(require('@/components/Idar_items.vue'));
	});
};

const Videos = resolve => {
	require.ensure(['@/components/Videos.vue'], () => {
		resolve(require('@/components/Videos.vue'));
	});
};

const Media = resolve => {
	require.ensure(['@/components/Media.vue'], () => {
		resolve(require('@/components/Media.vue'));
	});
};

const Styleguide = resolve => {
  require.ensure(['@/components/Styleguide.vue'], () => {
    resolve(require('@/components/Styleguide.vue'));
  });
};

const Test = resolve => {
  require.ensure(['@/components/Test.vue'], () => {
    resolve(require('@/components/Test.vue'));
  });
};

Vue.use(Router);

export default new Router({
	mode: 'history',
  	routes: [
		{path: '/', redirect: store.state.lang},
		{path: '/:lang/', component: Home},
		{path: '/:lang/idar', component: Idar},
		{path: '/:lang/idar/:filter', component: Idar_items},
		{path: '/:lang/fashion', component: Fashion},
		{path: '/:lang/fashion/videos', component: Videos},
		{path: '/:lang/fashion/:project_id', component: Gallery_fashion},
		{path: '/:lang/services', component: Services},
		{path: '/:lang/farnosova', component: Farnosova},
		{path: '/:lang/contact', component: Contact},
		{path: '/:lang/gallery/lastest/:gallery_category', component: Gallery_lastest},
		{path: '/:lang/gallery/idar/:project_id', component: Gallery_idar_project},
		{path: '/:lang/media', component: Media},
    {path: '/:lang/test', component: Test},
		{path: '/:lang/styleguide', component: Styleguide},
		{path: '/:lang/*', component: Page_404},
  ]
})


