<template>
  <div class="col-xs-12 info_divided">
    <div class="row">
      <div class="col-sm-6 col-lg-6 info-block match-height" :class="{'col-sm-push-6': (first == 'text')}">
        <slot name="image"></slot>
      </div>
      <div class="col-sm-6 col-lg-3 col-lg-offset-1 info-block match-height"
           :class="{'col-sm-pull-6': (first == 'text')}">
        <div class="table h100">
          <div class="table-cell vam">
            <slot name="text"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      first: {
        type: String,
        default: "image"
      }
    },
  }


</script>

<style lang="sass" src="./../../assets/style/info_devided.sass" scoped></style>


