import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
	state: {
		nav: {
			visible: false
		},
		lang: 'ru',
		currentPath: '/',
		//preloaderDone: false
		preloaderDone: true, //for dev purposes
		transitionName: 'fade',
    scrollIsDone: true,
		currentPageSlide: 1,
	},
	mutations: {
		setLang(state, lang){
			state.lang = lang;
		}
	},
	actions: {
		increment(context, lang){
			context.commit('setLang', lang);
		}
	}
});


